import React, { Component } from "react";

class Book extends Component {
  constructor() {
    super();
    this.state = {
      count: 0
    };
  }

  addClick = () => {
    this.setState({
      count: this.state.count + 1
    });
  };
  subsClick = () => {
    this.setState({
      count: this.state.count - 1
    });
  };
  resetClick = () => {
    this.setState({
      count: 0
    });
  };

  render() {
    const { img, title, author } = this.props.info;

    return (
      <div className="book">
        <img src={img} width="200" alt="book" />
        <div>
          <h3>Title: {title}</h3>
          <p>By: {author}</p>
          <h1>{this.state.count}</h1>
          <h2>{this.state.name}</h2>
          <button onClick={this.addClick}>Add</button>
          <button onClick={this.subsClick}>Substract</button>
          <button onClick={this.resetClick}>Reset</button>
        </div>
      </div>
    );
  }
}

export default Book;
